.chordsWiki_keyboard_container{
	position: relative;
	padding-bottom: 50%;
	height: 0;
}

.chordsWiki_keyboard {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	:after {
		@extend %clearfix;
	}
}
.chordsWiki_key {
	border-radius: 0px 0px 5px 5px;
	box-shadow: 0px 5px 1px rgba(32,32,32,0.2);
}

.chordsWiki_white_key{
	@extend .chordsWiki_key;
	float: left;
	position: relative;
	width: calc(100% / 7);
	border: 1px solid #959595;
	height: 100%;
	box-sizing: border-box;
}

.chordsWiki_white_key:not(:first-child){
	border-left:none;
}

.chordsWiki_black_key{
	@extend .chordsWiki_key;
	position: absolute;
	left: 65%;
	background-color: black;
	width: 70%;
	height: 55%;
	z-index: 1;
}

.chordsWiki_pressed_key{
	background-color: red;
}
